import React, { useState } from 'react';
import { ethers } from 'ethers';
import { useWeb3 } from '../contexts/Web3Context';
import { WarriorCard } from '../components/WarriorCard';
import toast from 'react-hot-toast';

export function Warriors({ warriors, loadWarriors, loadWarBalance }) {
  const { contracts } = useWeb3();
  const [isMinting, setIsMinting] = useState(false);

  const mintWarrior = async () => {
    if (!contracts.warriorNFT || !contracts.warToken) {
      toast.error('');
      return;
    }

    // 
    console.log('WarToken :', contracts.warToken.address);
    console.log('WarriorNFT :', contracts.warriorNFT.address);
    console.log('Provider:', contracts.warToken.provider);

    setIsMinting(true);
    try {
      //  WAR Token 
      try {
        const name = await contracts.warToken.name();
        console.log('WarToken :', name);
      } catch (e) {
        console.error(' WarToken :', e);
        toast.error('WAR Token ');
        return;
      }

      //  GameManager 
      let mintPrice;
      try {
        mintPrice = await contracts.warriorNFT.getMintPrice();
      } catch (e) {
        //  50 WAR
        mintPrice = ethers.utils.parseEther('50');
      }
      
      const priceInEth = ethers.utils.formatEther(mintPrice);
      
      // 
      const userAddress = await contracts.warToken.signer.getAddress();
      console.log(':', userAddress);
      
      const allowance = await contracts.warToken.allowance(
        userAddress,
        contracts.warriorNFT.address
      );
      
      // 
      if (allowance.lt(mintPrice)) {
        toast.loading(' WAR Token...', { id: 'mintWarrior' });
        const approveTx = await contracts.warToken.approve(
          contracts.warriorNFT.address,
          mintPrice
        );
        await approveTx.wait();
      }
      
      // gas
      let gasLimit;
      try {
        const estimatedGas = await contracts.warriorNFT.estimateGas.mintWarrior();
        gasLimit = estimatedGas.mul(150).div(100); // 50%
      } catch (e) {
        gasLimit = 500000;
      }

      toast.loading(` ${priceInEth} WAR...`, { id: 'mintWarrior' });
      const tx = await contracts.warriorNFT.mintWarrior({ gasLimit });
      const receipt = await tx.wait();

      // ID
      const event = receipt.events?.find(e => e.event === 'WarriorMinted');
      if (event) {
        const newId = event.args.tokenId.toString();
        toast.success(`  #${newId} `, { id: 'mintWarrior' });
      } else {
        toast.success(' ', { id: 'mintWarrior' });
      }

      // 
      setTimeout(async () => {
        await loadWarriors();
        await loadWarBalance();
      }, 1000);
    } catch (error) {
      console.error(':', error);
      toast.error(' : ' + error.message, { id: 'mintWarrior' });
    } finally {
      setIsMinting(false);
    }
  };

  return (
    <div className="space-y-4 md:space-y-6">
      {/*  */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-3">
        <div>
          <h3 className="text-xl md:text-2xl font-bold text-gradient mb-1"></h3>
          <p className="text-gray-400 text-xs md:text-sm"></p>
        </div>
        <button
          onClick={mintWarrior}
          disabled={isMinting}
          className="btn-success flex items-center space-x-2 whitespace-nowrap text-sm md:text-base"
        >
          {isMinting ? (
            <>
              <div className="w-4 h-4 md:w-5 md:h-5 border-2 border-white/30 border-t-white rounded-full animate-spin" />
              <span>...</span>
            </>
          ) : (
            <>
              <span className="text-lg md:text-xl"></span>
              <span className="hidden sm:inline"> (50 WAR)</span>
              <span className="sm:hidden"> (50)</span>
            </>
          )}
        </button>
      </div>

      {/*  */}
      {warriors.length === 0 ? (
        <div className="glass rounded-2xl p-6 md:p-12 text-center">
          <div className="text-6xl md:text-8xl mb-4 md:mb-6 float-animation"></div>
          <h4 className="text-lg md:text-xl font-bold text-white mb-2"></h4>
          <p className="text-gray-400 text-sm mb-4 md:mb-6"></p>
          <button
            onClick={mintWarrior}
            disabled={isMinting}
            className="btn-success text-sm md:text-base"
          >
            + 
          </button>
        </div>
      ) : (
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 md:gap-6">
          {warriors.map((warrior) => (
            <WarriorCard key={warrior.id} warrior={warrior} />
          ))}
        </div>
      )}
    </div>
  );
}
